<template>
  <div class="serverInfo">
    <el-breadcrumb separator="/" class="breadcrumb">
      <el-breadcrumb-item>服务器信息</el-breadcrumb-item>
      <el-breadcrumb-item>服务器详情信息表</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="warp">
      <template>
        <el-table
          :data="data.nodes"
          style="width: 100%">
          <el-table-column
            prop="name"
            label="服务器名称">
          </el-table-column>
          <el-table-column
            prop="node_region_name"
            label="线路">
          </el-table-column>
          <el-table-column
            prop="max_connections_count"
            label="最大连接人数">
          </el-table-column>
          <el-table-column
            prop="connections_count"
            label="当前接接人数">
          </el-table-column>
          <el-table-column
            prop="connections_percent"
            label="占比">
          </el-table-column>
          <el-table-column
            label="线路分配">
            <template scope="scope">
              <span>{{isEnabled(scope.row.is_enabled)}}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="bandwidth_percent"
            label="宽带占比">
          </el-table-column>
          <el-table-column
            prop="cpu_percent"
            label="CPU">
          </el-table-column>
          <el-table-column
            prop="memory_percent"
            label="内存">
          </el-table-column>
          <el-table-column
            label="总流量(M)">
            <template scope="scope">
              <span>{{scope.row.transfer | transfer}}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="network_speed"
            label="网速">
          </el-table-column>
        </el-table>
      </template>
    </div>
    <!--分页-->
    <el-pagination
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="25"
      layout="total, prev, pager, next, jumper"
      :total="totalSize"
      class="page">
    </el-pagination>
  </div>
</template>

<script>
  import * as API from '../../../api/api'
export default{
    components:{},
    data(){
        return {
          data:[],
          currentPage:1,
        }
    },
    computed:{
      totalSize(){
          return this.data.total_count
      }
    },
    methods:{
      isEnabled(data){
        return data?"分配":"未分配"
      },
      getInfo(parm){
        return  new Promise( (resolve,reject) => {
          const token = JSON.parse(window.sessionStorage.getItem('loginInfo')).token;
          this.$http({
            method:'GET',
            url:API.server_details_info,
            headers: {'Authorization': token},
            params:parm
          }).then(function(res){
            if(res.status == 200){
              resolve(res)
            }
          }).catch(function(err){
            reject(err)
          })
        })
      },
      /*分页*/
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getInfo({page:val})
      }
    },
    mounted(){

      this.getInfo().then(res=>{
          this.data = res.data.data
          console.log(this.data)
      })
    }
}
</script>

<style scoped>
  .serverInfo{
    padding: 10px;
  }
  .warp{
    text-align: left;
    padding: 10px;
    background-color:#fff;
    border:1px solid #D3DCE6;
    margin-top: 20px;
  }
  .page{
    text-align: right;
    margin-top: 20px;
  }
</style>

<style>
  .el-table th>.cell{
    text-align: center;
  }
</style>



